import { connect } from "react-redux";
import { getUser } from "../../../auth/reducers.js";
import { logout } from "../../../auth/actions.js";
import ApptfHeader from "../Header";

function mapStateToProps(state) {
  return {
    user: getUser(state)
  };
}

function mapDispatchToProps(dispatch) {
  return { logout: logout(dispatch) };
}

export default connect(mapStateToProps, mapDispatchToProps)(ApptfHeader);



