import React from "react";
import PropTypes from "prop-types";
import { Card, Typography, withStyles } from "@material-ui/core";
import ApptfPartyList from "../PartyList";

const styles = {
  card: {
    height: "5em",
    margin: "0.1em",
    padding: "0.5em"
  },
  typography: {
    paddingBottom: "1em"
  }
};

function ApptfPartyCalendarDay({ day, classes, parties }) {
  return (
    <Card className={classes.card}>
      <Typography className={classes.typography} variant="caption">{day}</Typography>
      <ApptfPartyList parties={parties} />
    </Card>
  );
}

ApptfPartyCalendarDay.propTypes = {
  classes: PropTypes.object,
  day: PropTypes.number.isRequired,
  parties: PropTypes.arrayOf(PropTypes.object),
};

export default withStyles(styles)(ApptfPartyCalendarDay);



