const url = "/api"
const endpoint = entity => (resource = "") => `${url}/${entity}/${resource}`;
export const auth = (resource) => `${url}/auth/${resource}`;
export const party = endpoint("party");
export const user = endpoint("user");

/* Return a promise that forwards the body if fetch goes well and rejects on
failure or http error code */
export function promiseBody(url, params) {
  return new Promise((resolve, reject) => {
    fetch(url, params)
      .then(res => {
        if (res.ok) {
          if (res.status !== 204) {
            res.json().then(json => resolve(json));
          } else {
            resolve();
          }
        } else {
          reject(res.status);
        }
      })
      .catch(err => reject(err));
  });
}



