import React, { Component } from "react";
import PropTypes from "prop-types";
import { GridList, GridListTile, withStyles } from "@material-ui/core";
import ApptfPartyCalendarDay from "../PartyCalendarDay";

const styles = {};

function month(month, startDay, endDay, parties, assistParty) {
  const monthDays = [];
  const now = new Date();

  for (let i = startDay; i <= endDay; i++) {
    const dayParties = [];
    const maxTimestamp = new Date(
      now.getFullYear(),
      month - 1,
      i + 1
    ).getTime();

    while (parties.length > 0 && parties[0].date < maxTimestamp) {
      dayParties.push(parties.pop());
    }

    monthDays.push(
      <GridListTile key={i}>
        <ApptfPartyCalendarDay
          month={month}
          day={i}
          parties={dayParties}
          assistParty={assistParty}
        />
      </GridListTile>
    );
  }

  return monthDays;
}

class PartyCalendar extends Component {
  componentWillMount() {
    this.props.fetchParties();
  }

  render() {
    const parties = [...this.props.parties];
    const assistParty = this.props.assistParty;

    const may = month(5, 28, 31, parties, assistParty);
    const june = month(6, 1, 30, parties, assistParty);
    const july = month(7, 1, 31, parties, assistParty);
    const august = month(8, 1, 31, parties, assistParty);
    const september = month(9, 1, 30, parties, assistParty);

    return (
      <GridList cellHeight="auto" cols={7}>
        {may}
        {june}
        {july}
        {august}
        {september}
      </GridList>
    );
  }
}

PartyCalendar.propTypes = {
  classes: PropTypes.object.isRequired,
  fetchParties: PropTypes.func.isRequired,
  parties: PropTypes.arrayOf(PropTypes.object),
};

export default withStyles(styles)(PartyCalendar);



