import React, { Component } from "react";
import PropTypes from "prop-types";
import { Chip } from "@material-ui/core";
import ApptfUserAvatar from "../UserAvatar";
import withUsers from "../withUsers";

class ApptfUserChip extends Component {

  render() {
    const { id, users, className } = this.props;
    if (!users) return null;
    const user = users.find(u => u.id === id);
    if (!user) return null;
    return (
      <Chip
        className={className}
        avatar={<ApptfUserAvatar user={user} />}
        label={user.name}
        clickable
        onClick={null}
      />
    );
  }
}

ApptfUserChip.propTypes = {
  id: PropTypes.string,
  users: PropTypes.arrayOf(PropTypes.object),
  className: PropTypes.string
};

export default withUsers(ApptfUserChip);



