import React, { Component } from "react";
import PropTypes from "prop-types";
import { Avatar } from "@material-ui/core";

class ApptfUserAvatar extends Component {

  avatarLetters(name) {
    return name
      .split(" ")
      .map(word => word[0])
      .join("");
  }

  render() {
    const { user } = this.props;
    if (!user) return null;
    return (
      <Avatar src={user.avatar} alt={`Avatar de ${user.name}`}>
        {this.avatarLetters(user.name)}
      </Avatar>
    );
  }
}

ApptfUserAvatar.propTypes = {
  user: PropTypes.object,
};

export default ApptfUserAvatar;



