import { connect } from "react-redux";
import { getParty, getLoading } from "../../reducers";
import { fetchParty, assistParty } from "../../actions";
import ApptfPartyView from "../PartyView";

function mapStateToProps(state) {
  return {
    party: getParty(state),
    loading: getLoading(state)
  };
}

function mapDispatchToProps(dispatch) {
  return {
    fetchParty: fetchParty(dispatch),
    assistParty: assistParty(dispatch)
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ApptfPartyView);



