import React, { Component } from "react";
import PropTypes from "prop-types";
import {
  Typography,
  AppBar,
  Toolbar,
  withStyles
} from "@material-ui/core";
import ApptfUserMenu from "../UserMenu";

const styles = {
  root: {
    flexGrow: 1
  },
  flex: {
    flex: 1
  },
  logo: {
    verticalAlign: "middle",
    width: "2em",
    height: "2em",
    margin: "0.1em 0.5em 0.2em 0.5em"
  }
};

class ApptfHeader extends Component {
  render() {
    const { classes, user, logout } = this.props;

    return (
      <AppBar position="sticky">
        <div className={classes.root}>
          <Toolbar>
            <Typography
              variant="title"
              color="inherit"
              className={classes.flex}
            >
              <img
                className={classes.logo}
                src="/logo.svg"
                alt="Logo de la AppTF"
              />
              A toda fiesta
            </Typography>
            { user && <ApptfUserMenu user={user} onLogout={logout} /> }
          </Toolbar>
        </div>
      </AppBar>
    );
  }
}

ApptfHeader.propTypes = {
  classes: PropTypes.object.isRequired,
  logout: PropTypes.func.isRequired,
  user: PropTypes.object
};

export default withStyles(styles)(ApptfHeader);



