import React, { Component } from "react";
import { connect } from "react-redux";
import { fetchUsers } from "../../actions";
import { getUsers } from "../../reducers";

class UsersProvider extends Component {
  componentWillMount() {
    this.props.fetchUsers();
  }

  render() {
    const { component, children, ...rest } = this.props;
    const Compo = component;
    return <Compo {...rest}>{children}</Compo>;
  }
}

const mapStateToProps = state => ({
  users: getUsers(state)
});

const mapDispatchToProps = dispatch => ({
  fetchUsers: fetchUsers(dispatch)
});

const ConnectedUsersProvider = connect(mapStateToProps, mapDispatchToProps)(UsersProvider);

function withUsers(Compo) {
  return function(props) {
    return <ConnectedUsersProvider component={Compo} {...props} />;
  };
}

export default withUsers;



