import React, { Component, Fragment } from "react";
import PropTypes from "prop-types";
import { withRouter } from "react-router";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Divider,
  Typography,
  withMobileDialog,
  withStyles
} from "@material-ui/core";
import { ApptfUserChip } from "../../../user";

const styles = {
  creator: {
    display: "inline-flex",
    marginTop: "0.5em",
    marginBottom: "0.5em"
  },
  creatorCaption: {
    lineHeight: "250%",
    marginRight: "1em"
  },
  assistantChip: {
    height: "1.5em",
    marginTop: "1em",
    marginBottom: "1em"
  }
};

class ApptfPartyView extends Component {
  handleClose = () => {
    this.props.history.goBack();
  };

  componentWillMount() {
    const { partyCreator, partyDate, fetchParty } = this.props;
    fetchParty({ creator: partyCreator, date: partyDate });
  }

  render() {
    const {
      party,
      fullScreen,
      match,
      classes,
      assistParty,
      loading
    } = this.props;

    if (!party || loading) {
      return null;
    }

    return (
      <Dialog
        open={
          party.creator === match.params.partyCreator &&
          party.date === parseInt(match.params.partyDate, 10)
        }
        fullScreen={fullScreen}
        onClose={this.handleClose}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle>{party.name}</DialogTitle>
        <DialogContent>
          <DialogContentText>{party.description}</DialogContentText>
          <span className={classes.creator}>
            <Typography className={classes.creatorCaption} variant="caption">
              {"Convocada por:"}
            </Typography>
            <ApptfUserChip id={party.creator} />
          </span>
          <div>
            <Typography variant="subheading">{"Asistentes"}</Typography>
            {party.assistants.map(userId => (
              <Fragment key={userId}>
                <ApptfUserChip id={userId} className={classes.assistantChip} />
                <Divider />
              </Fragment>
            ))}
          </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={this.handleClose}>{"Cerrar"}</Button>
          <Button
            onClick={() => assistParty(party)}
            variant="raised"
            color="primary"
          >
            {"Unirse"}
          </Button>
        </DialogActions>
      </Dialog>
    );
  }
}

ApptfPartyView.propTypes = {
  match: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
  party: PropTypes.object,
  fullScreen: PropTypes.bool.isRequired,
  fetchParty: PropTypes.func.isRequired,
  assistParty: PropTypes.func.isRequired,
  partyCreator: PropTypes.string.isRequired,
  partyDate: PropTypes.number.isRequired,
  loading: PropTypes.bool
};

export default withStyles(styles)(
  withMobileDialog()(withRouter(ApptfPartyView))
);



