import api from "./api";

export const FETCH_REQUEST = "PARTY_FETCH_REQUEST";
export const FETCH_SUCCESS = "PARTY_FETCH_SUCCESS";
export const FETCH_FAILURE = "PARTY_FETCH_FAILURE";

export const PARTY_REQUEST = "PARTY_PARTY_REQUEST";
export const PARTY_SUCCESS = "PARTY_PARTY_SUCCESS";
export const PARTY_FAILURE = "PARTY_PARTY_FAILURE";

export const ASSIST_REQUEST = "PARTY_ASSIST_REQUEST";
export const ASSIST_SUCCESS = "PARTY_ASSIST_SUCCESS";
export const ASSIST_FAILURE = "PARTY_ASSIST_FAILURE";

export function fetchParties(dispatch) {
  return () => {
    dispatch({
      type: FETCH_REQUEST
    });
    const promise = api.fetchParties();
    promise
      .then(res => {
        dispatch({
          type: FETCH_SUCCESS,
          payload: res
        });
      })
      .catch(err => {
        dispatch({
          type: FETCH_FAILURE,
          payload: err
        });
      });
    return promise;
  };
}

export function fetchParty(dispatch) {
  return (party) => {
    dispatch({
      type: PARTY_REQUEST
    });
    const promise = api.fetchParty(party);
    promise
      .then(res => {
        dispatch({
          type: PARTY_SUCCESS,
          payload: res
        });
      })
      .catch(err => {
        dispatch({
          type: PARTY_FAILURE,
          payload: err
        });
      });
    return promise;
  };
}

export function assistParty(dispatch) {
  return (party) => {
    dispatch({
      type: ASSIST_REQUEST
    });
    const promise = api.assistParty(party);
    promise
      .then(res => {
        dispatch({
          type: ASSIST_SUCCESS,
          payload: res
        });
      })
      .catch(err => {
        dispatch({
          type: ASSIST_FAILURE,
          payload: err
        });
      });
    return promise;
  };
}



