import { connect } from "react-redux";
import { session } from "../../actions.js";
import { getUser, getLoading } from "../../reducers.js";
import ApptfSession from "../Session";

function mapStateToProps(state) {
  return {
    user: getUser(state),
    loading: getLoading(state)
  };
}

function mapDispatchToProps(dispatch) {
  return { session: session(dispatch) };
}

export default connect(mapStateToProps, mapDispatchToProps)(ApptfSession);



