import React, { Fragment } from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import { Chip, withStyles } from "@material-ui/core";
import * as colors from "@material-ui/core/colors";
import ApptfConnectedPartyView from "../ConnectedPartyView";

const styles = {
  chip: {
    height: "1.5em",
    width: "100%"
  }
};

const colorNames = [
  "red",
  "pink",
  "purple",
  "indigo",
  "blue",
  "lightBlue",
  "cyan",
  "teal",
  "green",
  "lightGreen",
  "lime",
  "yellow",
  "amber",
  "orange",
  "brown"
];

function generateColor(str) {
  const code = str.split("").reduce((prev, ch) => prev + ch.charCodeAt(), 0);
  return {
    backgroundColor: colors[colorNames[code % colorNames.length]].A400
  };
}

function ApptfPartyList({ classes, parties }) {
  const tags = parties.map(party => (
    <Fragment key={party.creator}>
      <Chip
        className={classes.chip}
        style={generateColor(party.name)}
        key={party.creator}
        label={party.name}
        clickable
        component={Link}
        to={`party/${party.creator}/${party.date}`}
      />
      <ApptfConnectedPartyView
        partyCreator={party.creator}
        partyDate={party.date}
      />
    </Fragment>
  ));

  return tags;
}

ApptfPartyList.propTypes = {
  classes: PropTypes.object,
  parties: PropTypes.arrayOf(PropTypes.object)
};

export default withStyles(styles)(ApptfPartyList);



