import React, { Component } from "react";
import PropTypes from "prop-types";
import {
  IconButton,
  Menu,
  MenuItem,
  withStyles
} from "@material-ui/core";
import { ApptfUserAvatar } from "../../../user";

const styles = {};

class ApptfUserMenu extends Component {
  state = {
    anchorEl: null
  };

  handleMenu = event => {
    this.setState({ anchorEl: event.currentTarget });
  };

  render() {
    const menuOpen = Boolean(this.state.anchorEl);
    const { user, onLogout } = this.props;

    return (
      <div>
        <IconButton
          aria-owns={menuOpen ? "menu-appbar" : null}
          aria-haspopup="true"
          onClick={this.handleMenu}
          color="inherit"
        >
          <ApptfUserAvatar user={user} />
        </IconButton>
        <Menu
          id="menu-appbar"
          anchorEl={this.state.anchorEl}
          anchorOrigin={{
            vertical: "top",
            horizontal: "right"
          }}
          transformOrigin={{
            vertical: "top",
            horizontal: "right"
          }}
          open={menuOpen}
          onClose={this.handleClose}
        >
          <MenuItem onClick={this.props.onProfile}>Perfil</MenuItem>
          <MenuItem onClick={onLogout}>Cerrar sesión</MenuItem>
        </Menu>
      </div>
    );
  }
}

ApptfUserMenu.propTypes = {
  user: PropTypes.object.isRequired,
  onLogout: PropTypes.func.isRequired
};

export default withStyles(styles)(ApptfUserMenu);



