import { connect } from "react-redux";
import { register } from "../../actions.js";
import { getUser, getError, getLoading } from "../../reducers.js";
import ApptfRegisterView from "../RegisterView";

function mapStateToProps(state) {
  return {
    user: getUser(state),
    error: getError(state),
    loading: getLoading(state)
  };
}

function mapDispatchToProps(dispatch) {
  return { register: register(dispatch) };
}

export default connect(mapStateToProps, mapDispatchToProps)(ApptfRegisterView);



