import React, { Component } from "react";
import PropTypes from "prop-types";
import {
  Card,
  CardContent,
  CardActions,
  Typography,
  List,
  ListItem,
  TextField,
  Button
} from "@material-ui/core";
import { Link } from "react-router-dom";

class ApptfRegisterForm extends Component {
  constructor(props) {
    super(props);
    this.state = { id: "", password: "", passwordCheck: "", validate: false };
  }

  handleSubmit = () => {
    this.setState({ validate: true });
    if (this.validatePasswdMatch() && this.validatePasswdEmpty()) {
      this.props.onSubmit(this.state.id, this.state.password);
    }
  };

  validatePasswdMatch = () => this.state.password === this.state.passwordCheck;
  validatePasswdEmpty = () => this.state.password.length !== 0;

  render() {
    const errorPasswordMatch =
      this.state.validate && !this.validatePasswdMatch();
    const errorPasswordEmpty =
      this.state.validate && !this.validatePasswdEmpty();

    return (
      <Card>
        <CardContent>
          <Typography gutterBottom variant="headline" component="h2">
            Registrar nuevo usuario
          </Typography>
          <form onSubmit={this.handleSubmit}>
            <List>
              <ListItem>
                <TextField
                  autoFocus
                  id="id"
                  name="id"
                  label="Código de registro"
                  type="text"
                  onChange={e => this.setState({ id: e.target.value })}
                />
              </ListItem>
              <ListItem>
                <TextField
                  id="password"
                  name="password"
                  label="Contraseña"
                  type="password"
                  onChange={e => this.setState({ password: e.target.value })}
                  error={errorPasswordEmpty}
                />
              </ListItem>
              <ListItem>
                <TextField
                  id="password-check"
                  name="password-check"
                  label="Repite la contraseña"
                  type="password"
                  onChange={e =>
                    this.setState({ passwordCheck: e.target.value })
                  }
                  error={errorPasswordMatch}
                />
              </ListItem>
            </List>
          </form>
        </CardContent>
        <CardActions>
          <Link
            to="/login"
            style={{ marginRight: "auto", textDecoration: "none" }}
          >
            <Typography gutterBottom variant="caption" color="primary">
              Inicia sesión
            </Typography>
          </Link>
          <Button variant="raised" color="primary" onClick={this.handleSubmit}>
            Register
          </Button>
        </CardActions>
      </Card>
    );
  }
}

ApptfRegisterForm.propTypes = {
  onSubmit: PropTypes.func.isRequired
};

export default ApptfRegisterForm;



