import React, { Component } from "react";
import PropTypes from "prop-types";
import ApptfLoginForm from "../LoginForm";

class ApptfLoginView extends Component {

  handleLogin = (username, password) => {
    this.props.login(username, password).then(() => this.props.history.push("/"))
  }

  render() {
    const LoginView = (
      <div style={{ width: "90%", maxWidth: "400px", margin: "0 auto" }}>
        <ApptfLoginForm onSubmit={this.handleLogin} />
      </div>
    );
    return LoginView;
  }
}

ApptfLoginView.propTypes = {
  user: PropTypes.object,
  error: PropTypes.any,
  loading: PropTypes.bool.isRequired,
  login: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired
};

export default ApptfLoginView;



