import React from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import ApptfConnectedLoginView from "./components/ConnectedLoginView";
import ApptfConnectedRegisterView from "./components/ConnectedRegisterView";
import ApptfConnectedSession from "./components/ConnectedSession";

export default function({user, match }) {
  return user ? (
    <Redirect to="/" />
  ) : (
    <Switch>
      <Route path="/login" component={ApptfConnectedLoginView} />
      <Route path="/register" component={ApptfConnectedRegisterView} />
    </Switch>
  );
}

export const ApptfSession = ApptfConnectedSession;



