import React, { Fragment } from "react";
import { Route } from "react-router-dom";
import ApptfConnectedPartyCalendar from "./components/ConnectedPartyCalendar";

export default function() {
  const partyCalendar = props => <ApptfConnectedPartyCalendar {...props} />;

  return (
    <Fragment>
      <Route path="/party/:partyCreator/:partyDate" render={partyCalendar} />
      <Route path="/party" render={partyCalendar} />
    </Fragment>
  );
}



