import React, { Component, Fragment } from "react";
import PropTypes from "prop-types";
import { Provider } from "react-redux";
import { CssBaseline } from "@material-ui/core";
import { MuiThemeProvider, createMuiTheme } from "@material-ui/core/styles";
import ApptfAuth, { ApptfSession } from "./auth";
import ApptfParty from "./party";
import ApptfConnectedHeader from "./layout/components/ConnectedHeader";
import "./App.css";

const theme = createMuiTheme({
  palette: {
    primary: {
      light: "#bc477b",
      main: "#880e4f",
      dark: "#560027",
      contrastText: "#fff"
    },
    secondary: {
      light: "#fffff7",
      main: "#fff9c4",
      dark: "#cbc693",
      contrastText: "#3e2723"
    }
  }
});

class App extends Component {
  render() {
    return (
      <MuiThemeProvider theme={theme}>
        <Fragment>
          <CssBaseline />
          <Provider store={this.props.store}>
            <Fragment>
              <ApptfConnectedHeader />
              <ApptfSession authenticated={false}>
                {/* Here goes stuff that renders if you havent logged in */}
                <ApptfAuth />
              </ApptfSession>
              <ApptfSession authenticated={true}>
                {/* Here goes stuff that renders if you have a session */}
                <ApptfParty />
              </ApptfSession>
              <div className="filler" />
            </Fragment>
          </Provider>
        </Fragment>
      </MuiThemeProvider>
    );
  }
}

App.propTypes = {
  store: PropTypes.object.isRequired
};

export default App;



