import {
  FETCH_REQUEST,
  FETCH_SUCCESS,
  FETCH_FAILURE,
  ASSIST_REQUEST,
  ASSIST_SUCCESS,
  ASSIST_FAILURE,
  PARTY_REQUEST,
  PARTY_SUCCESS,
  PARTY_FAILURE
} from "./actions";

const initialState = {
  parties: [],
  party: null,
  loading: false
};

export default function party(state = initialState, action) {
  switch (action.type) {
    case FETCH_REQUEST:
    case ASSIST_REQUEST:
    case PARTY_REQUEST:
      return { ...state, loading: true };
    case FETCH_SUCCESS:
      return { ...state, loading: false, parties: action.payload };
    case ASSIST_SUCCESS:
      return { ...state, loading: false };
    case PARTY_SUCCESS:
      return { ...state, loading: false, party: action.payload };
    case FETCH_FAILURE:
    case ASSIST_FAILURE:
    case PARTY_FAILURE:
      return { ...state, loading: false, error: action.payload };
    default:
      return state;
  }
}

function getState(state) {
  return state.party;
}

export function getParties(state) {
  return getState(state).parties;
}
export function getParty(state) {
  return getState(state.party);
}
export function getLoading(state) {
  return getState(state).loading;
}



