import { connect } from "react-redux";
import { getParties } from "../../reducers";
import { fetchParties } from "../../actions";
import ApptfPartyCalendar from "../PartyCalendar";

function mapStateToProps(state) {
  return {
    parties: getParties(state)
  };
}

function mapDispatchToProps(dispatch) {
  return {
    fetchParties: fetchParties(dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ApptfPartyCalendar);



