import api from "./api";
import { getUsers, getLoading } from "./reducers";

export const FETCH_REQUEST = "USERS_FETCH_REQUEST";
export const FETCH_SUCCESS = "USERS_FETCH_SUCCESS";
export const FETCH_FAILURE = "USERS_FETCH_FAILURE";

export function fetchUsers(dispatch) {
  return function() {
    let promise = Promise.resolve();

    function fetchUsersThunk(dispatch, getState) {
      const users = getUsers(getState());
      const loading = getLoading(getState());

      if (loading) {
        return;
      }
      if (users) {
        dispatch({ type: FETCH_SUCCESS, payload: users });
        return;
      }

      dispatch({ type: FETCH_REQUEST });
      promise = api.fetchUsers();
      promise
        .then(res => {
          dispatch({ type: FETCH_SUCCESS, payload: res });
        })
        .catch(err => {
          dispatch({ type: FETCH_FAILURE, payload: err });
        });
    };

    dispatch(fetchUsersThunk);

    return promise;
  };
}



