import { party, promiseBody } from "../api-utils.js";

const headers = new Headers();
headers.append("Content-Type", "application/json");
headers.append("Accept", "application/json");

function fetchParties() {
  const params = {
    headers,
    method: "GET",
    credentials: "include"
  };

  return promiseBody(party(), params);
}

function fetchParty(partyData) {
  const params = {
    headers,
    method: "GET",
    credentials: "include"
  };

  return promiseBody(party(`${partyData.creator}/${partyData.date}`), params);
}

function assistParty(partyData) {
  const params = {
    headers,
    method: "POST",
    credentials: "include"
  };

  return promiseBody(
    party(`${partyData.creator}/${partyData.date}/assist`),
    params
  );
}

export default { fetchParties, fetchParty, assistParty };



