import { auth, promiseBody } from "../api-utils.js";

const headers = new Headers();
headers.append("Content-Type", "application/json");
headers.append("Accept", "application/json");

function session() {
  const params = {
    headers,
    method: "GET",
    credentials: "include"
  };

  return promiseBody(auth("session"), params);
}

function login(username, password) {
  const params = {
    headers,
    method: "POST",
    credentials: "include",
    body: JSON.stringify({ username, password })
  };

  return promiseBody(auth("login"), params);
}

function register(id, password) {
  const params = {
    headers,
    method: "POST",
    credentials: "include",
    body: JSON.stringify({ password })
  };

  return promiseBody(auth(`register/${id}`), params);
}

function logout() {
  const params = {
    headers,
    method: "GET",
    credentials: "include"
  };

  return promiseBody(auth("logout"), params);
}

export default { login, register, session, logout };



