import { connect } from "react-redux";
import { login } from "../../actions.js";
import { getUser, getError, getLoading } from "../../reducers.js";
import ApptfLoginView from "../LoginView";

function mapStateToProps(state) {
  return {
    user: getUser(state),
    error: getError(state),
    loading: getLoading(state)
  };
}

function mapDispatchToProps(dispatch) {
  return { login: login(dispatch) };
}

export default connect(mapStateToProps, mapDispatchToProps)(ApptfLoginView);



