import React, { Component } from "react";
import PropTypes from "prop-types";
import { Redirect } from "react-router";
import ApptfRegisterForm from "../RegisterForm";

class ApptfRegisterView extends Component {
  render() {
    const RegisterView = (
      <div style={{ width: "90%", maxWidth: "400px", margin: "0 auto" }}>
        <ApptfRegisterForm onSubmit={this.props.register} />
      </div>
    );
    return this.props.user ? <Redirect to="/" /> : RegisterView;
  }
}

ApptfRegisterView.propTypes = {
  user: PropTypes.object,
  error: PropTypes.any,
  loading: PropTypes.bool.isRequired,
  register: PropTypes.func.isRequired
};

export default ApptfRegisterView;



