import { user, promiseBody } from "../api-utils.js";

const headers = new Headers();
headers.append("Content-Type", "application/json");
headers.append("Accept", "application/json");

function fetchUsers() {
  const params = {
    headers,
    method: "GET",
    credentials: "include"
  };

  return promiseBody(user(), params);
}

export default { fetchUsers };



