import api from "./api.js";
import { getUser, getLoading } from "./reducers.js";

export const SESSION_REQUEST = "AUTH_SESSION_REQUEST";
export const SESSION_SUCCESS = "AUTH_SESSION_SUCCESS";
export const SESSION_FAILURE = "AUTH_SESSION_FAILURE";

export const LOGIN_REQUEST = "AUTH_LOGIN_REQUEST";
export const LOGIN_SUCCESS = "AUTH_LOGIN_SUCCESS";
export const LOGIN_FAILURE = "AUTH_LOGIN_FAILURE";

export const REGISTER_REQUEST = "AUTH_REGISTER_REQUEST";
export const REGISTER_SUCCESS = "AUTH_REGISTER_SUCCESS";
export const REGISTER_FAILURE = "AUTH_REGISTER_FAILURE";

export const LOGOUT_REQUEST = "AUTH_LOGOUT_REQUEST";
export const LOGOUT_SUCCESS = "AUTH_LOGOUT_SUCCESS";
export const LOGOUT_FAILURE = "AUTH_LOGOUT_FAILURE";

export function login(dispatch) {
  return (username, password) => {
    dispatch({ type: LOGIN_REQUEST });
    const promise = api.login(username, password);
    promise
      .then(res => {
        dispatch({ type: LOGIN_SUCCESS, payload: res });
      })
      .catch(err => {
        dispatch({ type: LOGIN_FAILURE, payload: err });
      });
    return promise;
  };
}

export function logout(dispatch) {
  return () => {
    dispatch({ type: LOGOUT_REQUEST });
    const promise = api.logout();
    promise
      .then(res => {
        dispatch({ type: LOGOUT_SUCCESS });
      })
      .catch(err => {
        dispatch({ type: LOGOUT_FAILURE });
      });
    return promise;
  };
}

export function register(dispatch) {
  return (id, passwd) => {
    dispatch({ type: REGISTER_REQUEST });
    const promise = api.register(id, passwd);
    promise
      .then(res => {
        dispatch({ type: REGISTER_SUCCESS, payload: res });
      })
      .catch(err => {
        dispatch({ type: REGISTER_FAILURE, payload: err });
      });
    return promise;
  };
}

export function session(dispatch) {
  return function() {
    let promise = Promise.resolve();

    function sessionThunk(dispatch, getState) {
      const user = getUser(getState());
      const loading = getLoading(getState());

      if (loading) {
        return;
      }
      if (user) {
        dispatch({ type: SESSION_SUCCESS, payload: { ...user } });
        return;
      }

      dispatch({ type: SESSION_REQUEST });
      promise = api.session();
      promise
        .then(res => {
          dispatch({ type: SESSION_SUCCESS, payload: res });
        })
        .catch(err => {
          dispatch({ type: SESSION_FAILURE });
        });
    };

    dispatch(sessionThunk);

    return promise;
  };
}



