import { FETCH_REQUEST, FETCH_SUCCESS, FETCH_FAILURE } from "./actions.js";

const initialState = {
  users: null,
  loading: false
};

export default function party(state = initialState, action) {
  switch (action.type) {
    case FETCH_REQUEST:
      return { ...state, loading: true };
    case FETCH_SUCCESS:
      return { ...state, loading: false, users: action.payload };
    case FETCH_FAILURE:
      return { ...state, loading: false, error: action.payload.err };
    default:
      return state;
  }
}

function getState(state) {
  return state.user;
}

export function getUsers(state) {
  return getState(state).users;
}
export function getLoading(state) {
  return getState(state).loading;
}



